import React from "react"
import { css } from "@emotion/react"

export default function BurgerMenuButton(props) {
  const burgerButtonStyle = css`
    font-size: 36px;
    z-index: 2;
  `

  function handleClick() {
    if (props.navOpen) {
      props.closeNav()
    } else {
      props.openNav()
    }
  }

  return (
    <div css={burgerButtonStyle}>
      <button
        onClick={handleClick}
        css={css`
          cursor: pointer;
          background: none;
          border: none;
          z-index: 2;
          font-size: 0.8em;
          width: 1em;
        `}
      >
        {String.fromCharCode(props.navOpen ? 0x2715 : 0x2630)}
      </button>
    </div>
  )
}
