import React, { useState } from "react";
import { css } from "@emotion/react";
import { rhythm } from "../utils/typography";
import { navigate } from "gatsby";
import SideNav from "./sidenav";
import BurgerMenuButton from "./burgerMenuButton";
import NavButton from "./navButton";
import FullscreenButton from "./fullscreenButton";
import CodePlaygroundButton from "./codePlaygroundButton";
import { SplitPane } from "react-collapse-pane";
import "highlight.js/styles/atom-one-dark.css";
import "highlight.js/styles/atom-one-light.css";

function Layout(props) {
  const [navOpen, setNavOpen] = useState(props && props.navOpen);
  const [theme, setTheme] = React.useState("dark");

  React.useEffect(() => {
    if (sessionStorage.getItem("lightMode")) {
      setTheme("light");
    } else {
      setTheme("dark");
    }
  });

  const onKeyDown = React.useCallback(event => {
    const { key } = event;
    switch (key) {
      case "ArrowLeft":
        navigatePrev();
        break;
      case "ArrowRight":
        navigateNext();
        break;
    }
  }, []);

  React.useEffect(() => {
    if (props.codePlaygroundOpen) {
      document.removeEventListener("keydown", onKeyDown);
    } else {
      document.addEventListener("keydown", onKeyDown);
    }
    return () => document.removeEventListener("keydown", onKeyDown);
  }, [props.codePlaygroundOpen]);

  React.useEffect(() => {
    setNavOpen(props && props.navOpen);
  }, [props.navOpen]);

  const layoutStyle = css`
    margin: 0 auto;
    //padding: ${rhythm(2)};
    padding-top: 0;
    //overflow: auto;

    display: flex;
    flex: 1 1 0%;
    height: 100%;
    position: absolute;
    outline: none;
    user-select: text;
    flex-direction: column;
    min-height: 100%;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;

    .footnotes {
      display: none;
    }

    .Pane.horizontal:nth-of-type(3) {
      box-shadow: 0px -3px 8px rgba(0, 0, 0, 0.2);
    }

    details summary {
      cursor: pointer;
      background-color: lightgrey;
      margin-bottom: 1em;
    }

    details summary::before {
      content: "";
      display: inline-block;
      transform: rotate(-90deg);
      margin-right: 1em;
    }

    details[open] summary::before {
      content: "";
      transform: rotate(0deg);
    }

    @media (max-width: 45em) {
      margin-left: 10px;
      margin-bottom: 3em;
      height: initial;
      min-height: initial;
    }
  `;
  const navClosedStyle = css`
    margin-left: 55px;
  `;
  const navOpenStyle = css`
    margin-left: 55px;
    @media (min-width: 45em) {
      margin-left: 305px;
    }
  `;

  const buttonMenuStyle = css`
    position: fixed;
    top: 0;
    z-index: 10;
    @media (max-width: 45em) {
      bottom: 0px;
      left: 0px;
      top: initial;
      display: flex;
      justify-content: space-around;
      align-items: baseline;
      width: 100%;
    }
  `;
  const buttonMenuNavClosedStyle = css`
    ${buttonMenuStyle};
    left: 5px;
  `;
  const buttonMenuNavOpenStyle = css`
    ${buttonMenuStyle};
    left: 255px;
    @media (min-width: 45em) {
      left: 255px;
    }
  `;

  const hidePane = css`
    .Pane:nth-of-type(3) {
      display: none;
    }
  `;

  function openNav() {
    setNavOpen(true);
  }

  function closeNav() {
    setNavOpen(false);
  }

  function navigatePrev() {
    navigate(props && props.prevSlug);
  }

  function navigateNext() {
    navigate(props && props.nextSlug);
  }

  return (
    <div className={theme}>
      <SideNav navOpen={navOpen} />
      <nav
        id="buttonMenu"
        css={navOpen ? buttonMenuNavOpenStyle : buttonMenuNavClosedStyle}
      >
        <BurgerMenuButton
          openNav={openNav}
          closeNav={closeNav}
          navOpen={navOpen}
          id="button-burgerMenu"
        ></BurgerMenuButton>
        <NavButton
          id="prevButton"
          handleClick={navigatePrev}
          charCode={0x21e6}
          show={props && props.prevSlug}
        ></NavButton>
        <NavButton
          id="nextButton"
          handleClick={navigateNext}
          charCode={0x21e8}
          show={props && props.nextSlug}
        ></NavButton>
        <FullscreenButton id="fullscreenButton"></FullscreenButton>
        {props.setCodePGOpen && (
          <CodePlaygroundButton
            setCodePGOpen={props.setCodePGOpen}
            codePlaygroundOpen={props.codePlaygroundOpen}
            id="button-codePlayground"
          ></CodePlaygroundButton>
        )}
      </nav>
      <div css={[layoutStyle, navOpen ? navOpenStyle : navClosedStyle]}>
        {
          <SplitPane
            split="horizontal"
            collapseOptions={{
              beforeToggleButton: (
                <div css={props.collapseButtonStyle}>
                  {String.fromCharCode(0x02c4)}
                </div>
              ),
              afterToggleButton: (
                <div css={props.collapseButtonStyle}>
                  {String.fromCharCode(0x02c5)}
                </div>
              ),
              buttonTransition: "zoom",
              buttonPositionOffset: -20,
              collapsedSize: 50,
              collapseTransitionTimeout: 350,
              collapseDirection: "up",
            }}
            resizerOptions={{
              css: (!props.codePlaygroundOpen && {
                display: "none",
              }) || {
                display: "block",
                backgroundColor: "rgba(120, 120, 120, 0.3)",
              },
            }}
            css={!props.codePlaygroundOpen && hidePane}
          >
            <div
              css={css`
                height: 100%;
                width: 100%;
                overflow: auto;
              `}
            >
              {props.children}
            </div>
            {props.codePlaygroundOpen && props.codeEditor}
          </SplitPane>
        }
      </div>
    </div>
  );
}
/*css={css`
              width: inherit !important;
            `}*/

export default React.memo(Layout);
