import React from "react"
import { css } from "@emotion/react"

export default function CodePlaygroundButton(props) {
  const codePlaygroundButtonStyle = css`
    font-size: 36px;
    z-index: 2;
  `

  function handleClick() {
    props.setCodePGOpen(!props.codePlaygroundOpen)
  }

  return (
    <div css={codePlaygroundButtonStyle}>
      <button
        onClick={handleClick}
        css={css`
          font-size: 28px;
          cursor: pointer;
          background: none;
          border: none;
        `}
      >
        {String.fromCharCode(0x276e) + String.fromCharCode(0x276f)}
      </button>
    </div>
  )
}
