import React from "react";
import { css } from "@emotion/react";
import { useStaticQuery, Link, graphql, withPrefix } from "gatsby";
import { rhythm } from "../utils/typography";
import { useLocation } from "@reach/router";

export default function SideNav(props) {
  const navItemStyle = css`
    padding: 4px 4px 4px 15px;
    display: block;
    @media (max-height: 30em) {
      font-size: 18px;
    }
  `;
  const navItemStyle1 = css`
    ${navItemStyle};
    padding-left: 25px;
    ::before {
      content: "-";
      position: absolute;
      left: 15px;
    }
  `;
  const navItemStyle2 = css`
    ${navItemStyle};
    padding-left: 30px;
    ::before {
      content: "--";
      position: absolute;
      left: 15px;
    }
  `;
  const navItemHighlightStyle = { backgroundColor: "hsla(0, 0%, 0%, 0.07)" };

  const navStyle = css`
    height: 100%;
    width: 250px;
    position: fixed;
    z-index: 1;
    top: 0;
    left: -250px;
    overflow-x: hidden;
    overflow-y: auto;
    background: white;
    border-right: 1px solid hsla(0, 0%, 0%, 0.07);
    @media (max-height: 30em) {
      padding-top: 15px;
    }
    @media (max-width: 45em) {
      bottom: 3em;
      height: initial;
    }
  `;
  const navClosedStyle = css`
    ${navStyle};
    left: -250px;
  `;
  const navOpenStyle = css`
    ${navStyle};
    left: 0px;
  `;

  const data = useStaticQuery(
    graphql`
      query {
        allMdx(sort: { fields: [frontmatter___date], order: ASC }) {
          totalCount
          edges {
            node {
              id
              frontmatter {
                title
                date(formatString: "DD MMMM, YYYY")
              }
              fields {
                slug
              }
              headings {
                depth
                value
              }
            }
          }
        }
        site {
          siteMetadata {
            title
          }
        }
      }
    `
  );
  const { edges: lectures } = data.allMdx;
  const pathname = useLocation().pathname;
  return (
    <nav id="mySidenav" css={props.navOpen ? navOpenStyle : navClosedStyle}>
      <Link to={`/`} key="link-root">
        <h3
          css={css`
            margin-bottom: ${rhythm(0.5)};
            margin-top: ${rhythm(0.5)};
            font-size: ${rhythm(0.65)};
            display: inline-block;
            font-style: normal;
            margin-left: 15px;
            text-color: black;
          `}
        >
          {data.site.siteMetadata.title}
        </h3>
        <hr />
      </Link>
      {lectures.map(({ node: lecture }) =>
        [
          <Link
            to={lecture.fields.slug + "" + 0}
            key={lecture.id + "" + 0}
            state={{ navOpen: props.navOpen }}
            css={navItemStyle}
            activeStyle={navItemHighlightStyle}
          >
            {lecture.frontmatter.title}
          </Link>,
        ].concat(
          pathname.startsWith(withPrefix(lecture.fields.slug))
            ? lecture.headings
                .filter(heading => heading.depth < 3)
                .map((heading, index) => (
                  <Link
                    to={lecture.fields.slug + "" + (index + 1)}
                    key={lecture.id + "" + (index + 1)}
                    state={{ navOpen: props.navOpen }}
                    css={heading.depth < 2 ? navItemStyle1 : navItemStyle2}
                    activeStyle={navItemHighlightStyle}
                  >
                    {heading.value}
                  </Link>
                ))
            : []
        )
      )}
    </nav>
  );
}
